import React, { useRef, useEffect } from 'react';

import CodeMirror from 'codemirror'
import 'codemirror/addon/comment/comment'
import 'codemirror/addon/selection/mark-selection'

import modeHosts from './components/Editor/cm_hl'

import './App.css';

modeHosts()

function App() {
  const ref = useRef<HTMLTextAreaElement>(null)

    useEffect(() => {
    if (!ref.current) return

    let cm = CodeMirror.fromTextArea(ref.current, {
      lineNumbers: true,
      // mode: "text/html",
      // readOnly: false,
      // mode: 'hosts',
    })

    cm.setSize('100%', '100%')
  }, [])

  return (
    <div className="App">
      <textarea className="hosts_editor" ref={ref}></textarea>
    </div>
  );
}

export default App;
